
import { defineComponent, ref, inject } from 'vue';

import { Task, Status } from '../types';

export default defineComponent({
	setup() {
		const taskList: Task[] = inject<any>('taskList');

		const taskURL = ref('');
		const taskName = ref('');

		let index = 0;
		const addTask = () => {
			const id = ++index;
			const task: Task = {
				id: id,
				name: taskName.value || `任务${id}`,
				url: taskURL.value,
				status: Status.Loading,
				progress: 0,
				chipArray: [],
			};
			taskList.push(task);
			taskURL.value = '';
			taskName.value = '';
		};

		return { taskURL, addTask, taskName };
	},
});
