import { Status, Chip, Task } from '../types';
import request from './request';

export default (chip: Chip, task: Task) => {
	const taskFunc = () => {
		request(
			chip.url,
			{
				success(data) {
					chip.data = data as ArrayBuffer;
					chip.status = Status.Ok;
					task.progress++;
				},
				fail() {
					chip.status = Status.Fail;
				},
			},
			true
		);
	};
	taskFunc.id = task.id;
	return taskFunc;
};
