export interface Chip {
	url: string;
	status: Status;
	data?: ArrayBuffer;
}
export enum Status {
	Loading='Loading',
	Ready = 'Ready',
	Ok = 'OK',
	Fail = 'Fail',
}
export interface Task {
	id: number;
	name: string;
	url: string;
	status: Status;
	progress: number;
	chipArray: Array<Chip>;
}
