
import { defineComponent, onMounted, PropType, reactive, ref, toRefs, watch, watchEffect } from 'vue';
import Detail from './Detail.vue';

import { Task, Status, Chip } from '../types';

import request from '../util/request';
import applyChipURL from '../util/applyChipURL';
import storeAllChips from '../util/storeAllChips';
import createChipTask from '../util/createChipTask';
import Manager from '../util/Manager';

export default defineComponent({
	components: { Detail },
	props: {
		task: { type: Object as PropType<Task>, required: true },
		manager: { type: Object as PropType<Manager>, required: true },
		remove: { type: Function, required: true },
	},
	setup(props) {
		const { task, manager } = toRefs(props);

		const retry = (chip: Chip) => {
			if (chip.status === Status.Fail) {
				chip.status = Status.Ready;
				props.manager.submit(createChipTask(chip, task.value));
			}
		};

		// 保存已下载的片段
		const save = () => {
			storeAllChips(
				task.value.chipArray.map(chip => chip.data as ArrayBuffer),
				task.value.name
			);
		};

		// 控制状态显示，以及下载片段完成后自动保存
		const taskStatusClass = ref('');
		watchEffect(() => {
			const status = task.value.status;
			const chipArray = task.value.chipArray;
			if (status === Status.Ready) {
				taskStatusClass.value = 'badge badge-primary';
				if (chipArray.length > 0 && chipArray.every(chip => chip.status === Status.Ok)) {
					task.value.status = Status.Ok;
					taskStatusClass.value = 'badge badge-success';
					save();
				}
				return;
			}
			if (status === Status.Fail) {
				taskStatusClass.value = 'badge badge-danger cursor';
				return;
			}
			if (status === Status.Loading) {
				taskStatusClass.value = 'badge badge-secondary';
				return;
			}
		});

		// 当任务建立后自动获取片段，并将下载片段的任务添加到管理中进行下载
		const requestTask = () => {
			request(task.value.url, {
				success(data) {
					const segments = (data as string).split('\n');
					const tsUrlList = segments.filter(segment => segment.toLowerCase().indexOf('.ts') > -1);
					if (tsUrlList.length === 0) {
						task.value.status = Status.Fail;
						return;
					}

					task.value.status = Status.Ready;
					tsUrlList.forEach(tsUrl => {
						const chip: Chip = reactive({
							url: applyChipURL(tsUrl, task.value.url),
							status: Status.Ready,
						});
						task.value.chipArray.push(chip);
						manager.value.submit(createChipTask(chip, task.value));
					});
				},
				fail() {
					task.value.status = Status.Fail;
				},
			});
		};
		onMounted(() => {
			requestTask();
		});

		const isShowDetail = ref(false); // 控制是否显示详情

		// 当失败后，重新请求任务
		const retryTask = () => {
			if (task.value.status === Status.Fail) {
				task.value.status = Status.Loading;
				requestTask();
			}
		};

		return { save, retry, isShowDetail, retryTask, taskStatusClass };
	},
});
