
import { defineComponent, inject, ref } from 'vue';
import TaskComp from './Task.vue';

import { Status, Chip, Task } from '../types';

import Manager from '../util/Manager';
import createChipTask from '../util/createChipTask';

export default defineComponent({
	components: { TaskComp },
	setup() {
		const taskList: Task[] = inject<any>('taskList');
		const manager = new Manager(20); // 创建片段下载管理

		// 暂停和开始
		const isRunning = ref(true);
		const changeWorkerStatus = () => {
			if (isRunning.value) {
				manager.stop();
			} else {
				manager.start();
			}
			isRunning.value = !isRunning.value;
		};

		// 删除任务
		const remove = (id: number) => {
			const index = taskList.findIndex(task => task.id === id);
			taskList.splice(index, 1);
			manager.remove(id);
		};

		// 重新下载所有错误片段
		const isRetring = ref(false);
		let timer: number | undefined = undefined;
		const autoRetryAll = () => {
			if (!isRetring.value) {
				timer = setInterval(() => {
					taskList.forEach((task: Task) => {
						task.chipArray.forEach((chip: Chip) => {
							if (chip.status === Status.Fail) {
								chip.status = Status.Ready;
								manager.submit(createChipTask(chip, task));
							}
						});
					});
				}, 2000);
			} else {
				clearInterval(timer);
			}
			isRetring.value = !isRetring.value;
		};

		return { taskList, manager, isRunning, changeWorkerStatus, isRetring, autoRetryAll, remove };
	},
});
