<template>
	<div class="card mb-2">
		<div class="card-header">添加任务</div>
		<div class="card-body">
			<div class="input-group mb-1">
				<div class="input-group-prepend">
					<span class="input-group-text">URL</span>
				</div>
				<input type="text" class="form-control" placeholder="请输入m3u8视频的链接" v-model="taskURL" />
			</div>
			<div class="input-group mb-1">
				<div class="input-group-prepend">
					<span class="input-group-text">自定义名称</span>
				</div>
				<input type="text" class="form-control" v-model="taskName" placeholder="默认为“任务+id”" />
			</div>
			<button @click="addTask" class="btn btn-primary">确定</button>
		</div>
	</div>
</template>

<script lang="ts">
import { defineComponent, ref, inject } from 'vue';

import { Task, Status } from '../types';

export default defineComponent({
	setup() {
		const taskList: Task[] = inject<any>('taskList');

		const taskURL = ref('');
		const taskName = ref('');

		let index = 0;
		const addTask = () => {
			const id = ++index;
			const task: Task = {
				id: id,
				name: taskName.value || `任务${id}`,
				url: taskURL.value,
				status: Status.Loading,
				progress: 0,
				chipArray: [],
			};
			taskList.push(task);
			taskURL.value = '';
			taskName.value = '';
		};

		return { taskURL, addTask, taskName };
	},
});
</script>

