<template>
	<div :class="detailClass"></div>
</template>

<script lang="ts">
import { defineComponent, PropType, ref, watchEffect } from 'vue';
import { Status, Chip } from '../types';

export default defineComponent({
	props: {
		chip: {
			type: Object as PropType<Chip>,
			required: true,
		},
	},
	setup(props) {
		const detailClass = ref<string>('rounded');
		watchEffect(() => {
			const status = props.chip.status;
			if (status === Status.Ready) {
				detailClass.value = 'rounded gray';
				return;
			}
			if (status === Status.Ok) {
				detailClass.value = 'rounded green';
				return;
			}
			detailClass.value = 'rounded red';
		});
		return { detailClass };
	},
});
</script>

<style scoped>
div {
	width: 20px;
	height: 20px;
	margin: 5px;
	display: inline-block;
}
.gray {
	background: gray;
}
.green {
	background: green;
}
.red {
	background: red;
	cursor: pointer;
}
</style>
